const DayOfWeek = ({ day, time }) => {
    if (day === "monday") {
      return (
        <>
          <span>{time}  </span>
        </>
      )
    };
    if (day === "tuesday") {
      return (
        <>
          <span>{time}  </span>
        </>
      )
    };
    if (day === "wednesday") {
      return (
        <>
          <span>{time}  </span>
        </>
      )
    };
    if (day === "thursday") {
      return (
        <>
          <span>{time}  </span>
        </>
      )
    };
    if (day === "friday") {
      return (
        <>
          <span>{time}  '</span>
        </>
      )
    };
    if (day === "saturday") {
      return (
        <>
          <span>{time}  </span>
        </>
      )
    };
    if (day === "sunday") {
      return (
        <>
          <span>{time}  </span>
        </>
      )
    }
    return (
      <>
        <h3>     </h3>
      </>
    );
};
  
export default DayOfWeek;