import { clientAPI } from '../../service/axios.config';
import DayOfWeek from 'Components/DayOfWeek/DayOfWeek';
import Input from 'react-phone-number-input/input';
import { ToastContainer } from 'react-toastify';
import {
  Wrapper,
  Dialog,
  KindStyle,
  SubBTN,
} from './SignUp.styled';
import { useEffect, useState } from 'react';
import * as yup from 'yup';

const SignUp = ({ Close, kind_trainee, day, time, date }) => {
  const [dayTranslate, setDayTranslate] = useState('');
  const [tel, setTel] = useState('');
  const [clientName, setClientName] = useState('');
  const [currentErrorName, setCurrentErrorName] = useState('');
  const [currentErrorTel, setCurrentErrorTel] = useState('');
  const [validForm, setValidForm] = useState(false);
  const [validFormName, setValidFormName] = useState(false);
  const [validFormTel, setValidFormTel] = useState(false);
  const [onSubmit, setOnSubmit] = useState({
    id: "",
    day_translate: "",
    info: {
      date: "",
      day: "",
      time: "",
      kind_trainee: "",
      name: "",
    }
  })

  useEffect(() => {
    const DayOfWeekTranslate = () => {
      if (day === "monday") {
        return (setDayTranslate(''));
      };
      if (day === "tuesday") {
        return (setDayTranslate(''));
      };
      if (day === "wednesday") {
        return (setDayTranslate(''));
      };
      if (day === "thursday") {
        return (setDayTranslate(''));
      };
      if (day === "friday") {
        return (setDayTranslate(`'`));
      };
      if (day === "saturday") {
        return (setDayTranslate(''));
      };
      if (day === "sunday") {
        return (setDayTranslate(''));
      };
      return (null);
    };
    DayOfWeekTranslate();
  }, [day]);

  useEffect(() => {
    setOnSubmit({
      id: tel,
      day_translate: dayTranslate,
      info: {
        date: date,
        day: day,
        time: time,
        kind_trainee: kind_trainee,
        name: clientName,
      }
    })
  }, [clientName, date, day, dayTranslate, kind_trainee, time, tel]);

  useEffect(() => {
    if (validFormName === true & validFormTel === true) {
      return (setValidForm(true));
    }
    return (setValidForm(false));
  }, [validFormName, validFormTel]);

  console.log(kind_trainee)

  const userName = yup.object({
    name: yup.string()
      .min(3, "'    3 ")
      .max(15, "'    15 ")
      .required("' '  "),
  });

  const userTel = yup.object({
    id: yup.string()
      .min(13, "    13 ")
      .max(13, "    13 ")
      .required("  '  "),
  });

  const validateUserName = (e) => {
    const name = e.target.value;
    setClientName(name);
    userName.validate({ name: name })
      .catch((err) => {
        setCurrentErrorName(err.errors);
        setValidFormName(false);
      });
    userName.isValid({ name: name })
      .then(function (valid) {
        if (valid === true) {
          setCurrentErrorName('');
          setValidFormName(true);
          return;
        }
      })
  };

  const ValidateUserID = (e) => {
    const userID = e;
    setTel(e);
    userTel.validate({ id: userID })
      .catch((err) => {
        setCurrentErrorTel(err.errors);
        setValidFormTel(false);
      });
    userTel.isValid({ id: userID })
      .then(function (valid) {
        if (valid === true) {
          setCurrentErrorTel('');
          setValidFormTel(true);
          return;
        }
      })
  };  
  
  const HandleSubmit = (e) => {
    e.preventDefault();
    if (validForm === true) {
      const { id, day_translate, info } = onSubmit;
      // const  = info.day;
      clientAPI.sendDataUsers({ id, day_translate, info });
      clientAPI.sendTgRecord({id, day_translate, clientName, kind_trainee, time, date});
      // new Promise(resolve => setTimeout(resolve, 500));
      // alert(JSON.stringify(onSubmit, null, 2));
      return;
    }
    return;
  };

  return (
    <Wrapper>
      <Dialog>
        {kind_trainee ?
        <>  
          <h4>  <KindStyle>{kind_trainee}  <DayOfWeek day={day} time={time} /></KindStyle></h4><br />
          <h4> ,   </h4><br />
          <form>
            <label htmlFor="name">
              ':</label><br />
              <input
                name="name"
                type="text"
                // onChange={e =>{setClientName(e.target.value)}}
              onChange={validateUserName}
            /><br />
            <div>
              {currentErrorName ?
                <div style={{ color: "red", width: "180px" }}>
                  <p style={{ fontSize: "14px" }}>{currentErrorName}</p>
                </div> :
                null
              }
            </div>
            <label htmlFor="id">
               :</label><br />
              <Input
                name="id"
                id="id"
                value={tel}
                onChange={ValidateUserID}
                country="UA"
                international
                withCountryCallingCode
            /><br />
              {currentErrorTel ?
                <div style={{ color: "red", width: "180px" }}>
                  <p style={{ fontSize: "14px" }}>{currentErrorTel}</p>
                </div> :
                null
              }
              <SubBTN type="button" onClick={Close}>
                
              </SubBTN>
              <SubBTN type="submit" onClick={HandleSubmit} disabled={!validForm}>
                
            </SubBTN>
            </form>
          </>
          :
          <>
            <p>       .  ,   </p>
            <SubBTN type="button" onClick={Close}>
              
            </SubBTN>
          </>}
        
      </Dialog>
      <div>
        <ToastContainer
          style={{ marginTop: '55px', marginLeft: '25px', width: '250px' }}
          position="top-left"
          autoClose={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          theme="dark"
        />
      </div>
    </Wrapper>
  )
};

export default SignUp;